import http from '../../common/http.js'
export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			pageNum: 1,
			giftList: [],
			list: [{
				image: '../../static/member_center/hy@3x.png',
				name: '会员专享价'
			}, {
				image: '../../static/member_center/zs@3x.png',
				name: '专属免单'
			}, {
				image: '../../static/member_center/kk@3x.png',
				name: '开卡礼包'
			}, {
				image: '../../static/member_center/tc@3x.png',
				name: '提成奖励'
			}, {
				image: '../../static/member_center/sr@3x.png',
				name: '生日福利'
			}, {
				image: '../../static/member_center/jf@3x.png',
				name: '积分加成'
			}, ]
		}
	},
	onLoad: function() {

		this.allRequest()
	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {
			this.getPackage()
		},
		nowOpenCard: function(e) {
			console.log(e)
			uni.navigateTo({
				url: "./giftInfo?goods_id=" + e
			})
		},
		getPackage: function() {
			let opts = {
				url: 'Package/getPackage',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				pageSize: 10,
				pageNum: this.pageNum
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				this.giftList = res.data.data
			}, error => {
				console.log(error);
			})
		}
	}
}
